import * as React from 'react'
import { H1 } from '@blueprintjs/core'
import { graphql } from 'gatsby'
import { SiteLayout } from '../components/SiteLayout'
import { PostCard } from '../components/PostCard'

import './index.css'

const IndexPage = ({ data }) => {
  console.log(data)
  const {
    allMarkdownRemark: { edges }
  } = data
  return (
    <SiteLayout>
      <H1>James' Blog ðŸš€</H1>
      {edges.map(
        ({
          node: {
            frontmatter: { title, thumbnail },
            fields: { slug }
          }
        }) => (
          <PostCard slug={slug} thumbnail={thumbnail} title={title} />
        )
      )}
    </SiteLayout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark {
      pageInfo {
        perPage
      }
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            thumbnail_alt
            thumbnail {
              name
            }
            date
            slug
            meta_title
            seo_description
            seo_keywords
          }
        }
      }
    }
  }
`
export default IndexPage
