import * as React from 'react'
import { H2, Card } from '@blueprintjs/core'
import styled from 'styled-components'
import { StaticImage } from 'gatsby-plugin-image'
import { getSpacing } from '../stylesheet'
import { navigate } from 'gatsby'

export const PostCard = ({ title, slug, thumbnail }) => {
  console.log('thumbnail', `../blog${slug}/${thumbnail}`)

  const onNavigate = () => navigate(slug)
  return (
    <Card elevation={4} interactive={true} onClick={onNavigate}>
      <H2>{title}</H2>
      <StaticImage src={`../blog${slug}/${thumbnail}`} />
    </Card>
  )
}
